@import 'components/variables';

.section-carrito {
  padding-top: 50px;
  background-image: url(../images/team/bg-team-mobile.png);
  background-position: center top;
  background-repeat: no-repeat;
  background-size: cover;
  @media (min-width: 992px) {
    background: #efeff7;
    background-image: url(../images/team/bg-team.png);
    background-position: center top;
    background-repeat: no-repeat;
    background-size: contain;
    padding-top: 150px;
    padding-bottom: 3rem;
  }
  .content-container {
    background-color: $white;
    padding: 0;
    @media (max-width: 991.98px) {
      max-width: 100%;
    }
    @media (min-width: 576px) {
      border-radius: 25px;
    }
    @media (min-width: 992px) {
      background: $white;
      padding-bottom: 5rem;
    }
  }
  .container1 {
    background: transparent;
    border-top-left-radius: 25px;
    border-top-right-radius: 25px;
    padding-left: 1rem;
    padding-right: 1rem;
    @media (min-width: 992px) {
      background: $white;
    }
    @media (max-width: 991.98px) {
      p {
        color: $red;
      }
    }
  }
  .container2 {
    background: $white;
    @media (max-width: 991.98px) {
      max-width: 100%;
      padding-bottom: 4rem;
    }
    @media (min-width: 992px) {
      border-bottom-left-radius: 25px;
      border-bottom-right-radius: 25px;
      padding-bottom: 4rem;
      padding-left: 3rem;
      padding-right: 3rem;
    }
    .col-izq {
      order: 2;
      @media (min-width: 992px) {
        order: 1;
      }
      .pedido-title {
        color: $orange;
        border-bottom: 1px solid $text-gray;
        padding-bottom: 0.5rem;
      }
      .codigos {
        width: 100%;
        display: flex;
        @media (min-width: 768px) {
          width: 80%;
        }
        input {
          width: 70%;
          display: inline-block;
        }
        button {
          width: calc(30% - 0.5rem);
          margin-left: 0.5rem;
          display: inline-block;
          padding-left: 0;
          padding-right: 0;
        }
      }
      .form-control:focus {
        outline: none;
        box-shadow: none;
        border-color: #aefaf294;
      }
      .content-radio-btn {
        position: relative;
        padding-bottom: 1rem;
        .form-check-label {
          width: 30%;
          @media (min-width: 768px) {
            width: 20%;
          }
        }
        .radiobtn {
          width: 100%;
          opacity: 0.5;
        }
        .separador-vertical {
          width: 1px;
          height: 32px;
          background-color: #e4002b;
          border: none;
          display: inline-block;
          position: relative;
          transform: translateY(12px);
        }
        input[type="radio"]:checked+label .radiobtn {
          color: $red;
          font-weight: 600;
          background-color: $gray-light;
          opacity: 1;
        }
        input[type="radio"] {
          display: none;
        }
      }
      .address {
        a {
          color: $red;
          font-size: 0.8rem;
          text-decoration: none;
          transition: all 0.3s;
          &:hover {
            color: $orange;
          }
        }
      }
      .pasarela-pago {
        width: 100%;
        height: 100px;
        display: flex;
        align-items: center;
        justify-content: center;
        border: 1px solid #BDBDBD;
        background-color: #f7f7f7;
        border-radius: 2px;
      }
    }
    .col-der {
      order: 1;
      padding-right: calc(var(--bs-gutter-x) * 1);
      @media (min-width: 992px) {
        order: 2;
        padding-right: calc(var(--bs-gutter-x) * .5);
      }
      .content-productos {
        max-height: 60vh;
        overflow-y: auto;
        padding-right: calc(8.33333333% - (var(--bs-gutter-x) * .5) - 6px + 9px);
        &::-webkit-scrollbar {
          -webkit-appearance: none;
        }
        &::-webkit-scrollbar:vertical {
          width: 6px;
        }
        &::-webkit-scrollbar-button {
          display: none;
        }
        &::-webkit-scrollbar-thumb {
          background-color: $text-gray;
          border-radius: 12px;
        }
        &::-webkit-scrollbar-track {
          background-color: $gray-light;
          border-radius: 12px;
        }
        @media (min-width: 992px) {
          padding-right: calc(var(--bs-gutter-x) * .5);
        }
      }
      .producto {
        border-top: 1px solid $text-gray;
        padding-top: 1rem;
        padding-bottom: 1rem;
        position: relative;
        .content-img-product {
          width: 100%;
          padding-bottom: 100%;
          position: relative;
          img {
            width: 100%;
            height: 100%;
            object-fit: cover;
            object-position: center;
            position: absolute;
          }
        }
        .precio {
          font-size: 1.2rem;
        }
        .content-cantidad {
          display: inline-block;
          padding: 2px;
          background-color: $gray-light;
          border-radius: 5px;
          margin-top: 0.5rem;
          input.cantidad {
            text-align: center;
            border: none;
            background: transparent;
            margin: 0 1rem;
            color: $text-gray;
            font-size: 1rem;
            width: 35px;
            padding: 0 5px;
            @media (max-width: 500px) {
              margin: 0;
            }
          }
          input.cantidad:focus-visible {
            outline: none;
          }
          input.cantidad::-webkit-outer-spin-button,
          input.cantidad::-webkit-inner-spin-button {
            -webkit-appearance: none;
            margin: 0;
          }
          input.cantidad {
            -moz-appearance: textfield;
          }
          .mas,
          .menos {
            border: none;
            background-color: transparent;
            color: $text-gray;
            font-size: 0.8rem;
            padding: 0 5px;
            transition: all 0.3s;
            &:hover {
              color: $red;
            }
          }
        }
        .delete {
          color: $red;
          transition: all 0.3s;
          position: absolute;
          bottom: 0.5rem;
          right: 0.5rem;
          &:hover {
            color: $text-gray;
          }
        }
      }
      hr {
        background-color: $text-gray;
        opacity: 1;
      }
      .totales {
        padding-right: calc(8.33333333% - (var(--bs-gutter-x) * .5) + 9px);
        @media (min-width: 992px) {
          padding-right: initial;
        }
        p {
          text-align: end;
          font-size: 1.2rem;
          margin-bottom: 0.5rem;
        }
        .subtotal,
        .descuentos,
        .total {
          font-weight: 700;
        }
      }
    }
  }
}